// Lightmode colors
$primary: #2b3af7;
$primary-2: #424ff8;
$primary-text: #fdfcf9;
$secondary:  #702bf7;
$base-bg: #FFFFFF;
$base-bg-2: #f8f9ff;
$base-bg-3: #f1f3f4;
$base-bg-4: #333333;
$base-text: #0d042b;
$base-text-2: #3b364c;
$logo-text:  #FFFFFF;
$menu-text:  #FFFFFF;

// Darkmode colors
$primary-dark: #2b3af7;
$primary-2-dark: #424ff8;
$primary-text-dark: #fdfcf9;
$secondary-dark:  #702bf7;
$base-bg-dark: #121418;
$base-bg-2-dark: #1d2026;
$base-bg-3-dark: #24272d;
$base-bg-4-dark: #24272d;
$base-text-dark: #F4F4F5;
$base-text-2-dark: #D1D5DB;
$logo-text-dark: #F4F4F5;
$menu-text-dark:  #F4F4F5;

$zs-colors-light: (
    "primary": $primary,
    "primary-2": $primary-2,
    "primary-text": $primary-text,
    "secondary": $secondary,
    "base-bg": $base-bg,
    "base-bg-2": $base-bg-2,
    "base-bg-3": $base-bg-3,
    "base-bg-4": $base-bg-4,
    "base-text": $base-text,
    "base-text-2": $base-text-2,
    "logo-text": $logo-text,
    "menu-text": $menu-text
);

$zs-colors-dark: (
    "primary": $primary-dark,
    "primary-2": $primary-2-dark,
    "primary-text": $primary-text-dark,
    "secondary-dark": $secondary-dark,
    "base-bg": $base-bg-dark,
    "base-bg-2": $base-bg-2-dark,
    "base-bg-3": $base-bg-3-dark,
    "base-bg-4": $base-bg-4-dark,
    "base-text": $base-text-dark,
    "base-text-2": $base-text-2-dark,
    "logo-text": $logo-text-dark,
    "menu-text": $menu-text-dark
);

$zs-colors-light-css-vars: ();
@each $color, $value in $zs-colors-light {
  $zs-colors-light-css-vars: map-merge(
    $zs-colors-light-css-vars,
    (#{$color}: var(--color-#{$color}))
  )
}

$font-family-heading: Lato, -apple-system, serif;
$font-family-base: Source Sans Pro, -apple-system, "Helvetica Neue", Arial, sans-serif;
$font-family-monospace: Fira Mono, SFMono-Regular, Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-logo: Lato, -apple-system, "Helvetica Neue", Arial, sans-serif;

:root{
  @each $color, $value in $zs-colors-light {
    --color-#{$color}: #{$value};
  }
}

html[data-mode='dark'] {
  @each $color, $value in $zs-colors-dark {
      --color-#{$color}: #{$value};
  }
}

@import 'style';
